* {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}
@keyframes fall {
  from {
    margin-top: -200vh;
  }
  to {
    margin-top: 0;
  }
}
.button {
  font-size: 1em;
  border: 2px solid #aaa;
  margin: 10px;
  border-radius: 10px;
  padding: 10px;
}
button:focus { outline: none;}